<template>
    <div class='content'>
        <div class="breadcrumb">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class='search' @keyup.enter="onSearch()">
            <el-form :inline="true" :model="formInline" class="demo-form-inline">
               <el-row>
                <el-col :span="24">
                    <el-form-item label="">
                        <el-input class="inline-input"  v-model='formInline.searcharea' placeholder=""></el-input>
                    </el-form-item> 
                    <el-form-item label="">
                        <el-input class="inline-input"  v-model='formInline.searchname' placeholder=""></el-input>
                    </el-form-item> 
                     <el-form-item label="">
                        <el-input class="inline-input"  v-model='formInline.searchphone' placeholder=""></el-input>
                    </el-form-item>  
                    <el-form-item label="">
                        <el-date-picker v-model="formInline.fromDate" align="right" type="date" placeholder="" :picker-options="pickerOptions"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="">
                        <el-date-picker v-model="formInline.toDate" align="right" type="date" placeholder="" :picker-options="pickerOptions"></el-date-picker>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="onSearch()" icon="search"></el-button>
                    </el-form-item>
                </el-col>
              </el-row>
            </el-form>
        </div>
        <div class="userlist">
            <el-table :data="tableData" stripe border style="width: 100%" :height="$store.state.tableHeight" type="selection" v-loading="loading">
                <el-table-column prop="sn" label="" width="180"></el-table-column>
                <el-table-column prop="username" label=""></el-table-column>
                <el-table-column prop='userphoneNumber' label=""></el-table-column>              
                <el-table-column prop="remark" label="" width="180"></el-table-column>
                <el-table-column prop="amount"  label=""></el-table-column>
                <el-table-column prop="PayType"  label=""></el-table-column>
                <el-table-column prop="userarea" label="" width="180"></el-table-column>
                <el-table-column prop="createDate" label=""></el-table-column>
            </el-table>
            <div class="pagination" v-if="show">
                <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-size="pageSize"
                    layout="total, prev, pager, next"
                    :total="total">
                </el-pagination>
            </div>
        </div>       
    </div>
</template>

<script>
  import * as SITEURL from '@/store/app-urls'
  export default {
    name: 'user',
    data () {
      return {
        formInline: {
          searcharea: '',
          searchname: '',
          searchphone: '',
          fromDate: '',
          toDate: ''
        },
        tableData: [],
        currentPage: 1,  // 
        pageSize: 1,  // 
        total: 1,  // 
        loading: false,
        fromDate: '',
        endDate: '',
        type: '',
        show: false,
        pickerOptions: {
          shortcuts: [{
            text: '',
            onClick (picker) {
              picker.$emit('pick', new Date())
            }
          }, {
            text: '',
            onClick (picker) {
              const date = new Date()
              date.setTime(date.getTime() - 3600 * 1000 * 24)
              picker.$emit('pick', date)
            }
          }, {
            text: '',
            onClick (picker) {
              const date = new Date()
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', date)
            }
          }]
        }
      }
    },
    created: function () {
      this.loading = true
      this.loadingsetTimeout()
    },
    methods: {
      loadingsetTimeout () {
        setTimeout(() => { this.loading = false }, 800)
      },
      onSearch (page = 1) {
        var area = this.formInline.searcharea.trim()
        var name = this.formInline.searchname.trim()
        var phone = this.formInline.searchphone.trim()
        let are = '&area='
        this.loading = true
        let num = ''
        if (!area || area === '') {
          num = 1
          are = ''
        }
        this.$axios.get(SITEURL.TRAINING_SEARCH + '?page=' + page + '&limit=15' + '&first_time=' + this.formInline.fromDate + '&last_time=' + this.formInline.toDate + '&name=' + name + '&phone=' + phone + '&type=' + num + are + area).then(response => {
          if (response.data.success) {
            this.loading = false
            this.show = true
            this.tableData = response.data.data.data
            this.total = response.data.data.total
            this.pageSize = parseInt(response.data.data.per_page)
          } else {
            this.$message.warning(response.data.message)
          }
        }).catch(error => {
          this.loading = false
          console.log(error)
        })
      },
      handleSizeChange (val) {
        console.log(` ${val} `)
      },
      handleCurrentChange (val) {
        this.loading = true
        this.onSearch(val)
      }
    }
  }
</script>



<style scoped>
     .el-form-item {
        margin-bottom: 0 !important;
    }
    .breadcrumb {
        margin: 20px 0 20px 0;
    }
    .search {
        padding: 20px;
        background: #d3dce6;
    }
    .userlist {
        padding-top: 20px;
    }
    .demo-table-expand {
        font-size: 0;
    }
    .demo-table-expand label {
        width: 100px;
        color: #99a9bf;
    }
    
    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 30%;
    }
    .inline-input {
        width: 170px;
    }
    
</style>



